.container {
  margin: -24px;

  :global {
    .ant-collapse>.ant-collapse-item>.ant-collapse-header {
      color: #fff;
    }

    .ant-collapse>.ant-collapse-item>.ant-collapse-content {
      color: #fff;
    }
  }
}

.list {
  margin: -12px 0;

  .listItem {
    display: flex;
    padding: 12px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);

    &:last-child {
      border-bottom: none;
    }

    &.rowDirection {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }

    &.colDirection {
      flex-direction: column;
    }

    :global {
      .ant-segmented {
        background-color: rgba(255, 255, 255, 0.5);
        // .ant-segmented-item-selected {
        //   background-color: rgba(255, 255, 255, 0.5);
        // }
      }
    }
  }
}

.themeCards {
  margin: -8px 0;
  .themeCard {
    border-radius: 4px;
    margin: 8px 0;
    border-radius: 4px;
    padding: 0;
    position: relative;
    border: 2px solid #fff;
    background-color: #fff;
    &.pointer {
      cursor: pointer;
    }

    .mapBase,
    .mapCover {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }

    .mapBase {
      z-index: 1;
      background-image: url(./images/map_bg.png);
    }

    .mapCover {
      z-index: 2;
      background-image: url(./images/map_cover.png);
    }

    .mapText {
      line-height: 64px;
      color: #fff;
      text-shadow: 1px 1px rgba(0, 0, 0, 0.85), -1px -1px rgba(0, 0, 0, 0.85), 1px -1px rgba(0, 0, 0, 0.85), -1px 1px rgba(0, 0, 0, 0.85);
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 3;
    }
    .mapPanel {
      height: 64px;
      position: relative;
    }
    .mapSetting {
      padding: 2px 4px;
      font-size: 12px;
      color: #000;
      background-color: #fff;
      .mapSettingRow {
        align-items: center;
        .mapSettingSlider {
          padding: 0 8px;
        }
        .mapSettingColor {
          padding: 0 6px;
          :global {
            .ant-color-picker-trigger {
              width: 100%;
              justify-content: flex-start;
            }
            .ant-color-picker-alpha-input {
              display: none;
            }
            .ant-color-picker-slider-alpha {
              display: none;
            }
            .ant-color-picker-slider {
              padding: 10px 0;
            }
          }
        }
      }
    }
  }
}
