.map {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  background: #021423;
  background-color: var(--tmap-container-bcolor) !important;

  :global {
    .tdt-tile-pane .tdt-layer {
      &:first-child {
        img {
          opacity: var(--tmap-base-opacity) !important;
          filter: grayscale(var(--tmap-base-grayscale)) invert(var(--tmap-base-invert)) hue-rotate(var(--tmap-base-hue-rotate));
        }
      }

      &:last-child {
        img {
          opacity: var(--tmap-cover-opacity) !important;
          filter: grayscale(var(--tmap-cover-grayscale)) invert(var(--tmap-cover-invert)) hue-rotate(var(--tmap-cover-hue-rotate));
        }
      }
    }

    .tdt-control-container {
      .tdt-bottom {
        &.tdt-left {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
        }
      }
    }
  }
}
