.wrap {
  .drawerWrapBtn {
    width: 18px !important;
    height: 104px;
    position: absolute;
    left: -18px;
    top: 50%;
    margin-top: -52px;
    transform: perspective(10px) rotateY(-5deg);
    border-radius: 0;
    background-color: rgba(9,44,73,0.8) !important;
    border: 0;
  }
  .mainContainer {
    background-color: rgba(9,44,73,0.8) !important;
    color: #fff;
  }
  .settingWrap {
    :global {
      .ant-drawer-content-wrapper {
        transform: translateX(100%);
      }
    }
    .settingContainer {
      background-color: rgba(9,44,73,0.9) !important;
      color: #fff;
    }
  }
  .drawerHeader {
    color: #fff;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &.micro {
      justify-content: flex-start;
    }
    :global {
      .anticon {
        cursor: pointer;
        margin-left: 8px;
      }
    }
  }
  :global {
    .ant-drawer-content-wrapper-hidden {
      display: block;
      transform: translateX(100%);
    }

  }
}
