.container {
  margin: -24px;
  padding: 12px 0;
  :global {
    .ant-list-item {
      border-color: rgba(255,255,255,0.05);
    }
  }
  .item {
    cursor: pointer;
    padding: 12px 24px;
    &:hover {
      background: rgba(0,0,0,0.15);
    }
  }
  .action {
    font-size: 16px;
    color: rgba(255,255,255,0.85);
  }
}
